"use client";
import { useState, useEffect } from 'react';
import { auth, db } from '@/lib/firebase';
import { collection, addDoc, serverTimestamp, query, where, getDocs } from 'firebase/firestore';
import { onAuthStateChanged } from 'firebase/auth';
import Link from 'next/link';
import { useRouter, useSearchParams } from 'next/navigation';

export default function StudentDashboard() {
  const [user, setUser] = useState(null);
  const [status, setStatus] = useState('');
  const [userName, setUserName] = useState('Student');
  const [showPostSubmitMessage, setShowPostSubmitMessage] = useState(false); // New state
  const router = useRouter();
  const searchParams = useSearchParams(); // Hook to read URL parameters

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      if (currentUser) {
        setUser(currentUser);
        const nameFromEmail = currentUser.email.split('@')[0];
        setUserName(nameFromEmail.charAt(0).toUpperCase() + nameFromEmail.slice(1));
      } else {
        router.push('/login');
      }
    });
    
    // Check for the 'submitted' parameter in the URL when the component mounts
    if (searchParams.get('submitted') === 'true') {
        setShowPostSubmitMessage(true);
    }
    
    return () => unsubscribe();
  }, [router, searchParams]);

  const handleRequestChat = async () => {
    if (!user) return;
    setStatus('Checking for existing requests...');
    const q = query(collection(db, "chatQueue"), where("studentId", "==", user.uid));
    const querySnapshot = await getDocs(q);
    if (!querySnapshot.empty) {
        setStatus('You already have a pending chat request.');
        const existingSessionId = querySnapshot.docs[0].data().sessionId;
        router.push(`/student/chat/${existingSessionId}`);
        return;
    }

    setStatus('Sending request...');
    const sessionRef = await addDoc(collection(db, "chatSessions"), {
        studentId: user.uid,
        createdAt: serverTimestamp(),
        status: 'pending'
    });
    await addDoc(collection(db, "chatQueue"), {
        studentId: user.uid,
        studentEmail: user.email,
        sessionId: sessionRef.id,
        requestedAt: serverTimestamp()
    });
    setStatus('Chat request sent! Waiting for a professional.');
    router.push(`/student/chat/${sessionRef.id}`);
  };

  if (!user) return <p className="text-center mt-20">Loading...</p>;

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white shadow-sm">
        <div className="max-w-4xl mx-auto py-6 px-4">
          <h1 className="text-3xl font-bold text-gray-800">Welcome, {userName}!</h1>
          <p className="text-gray-500 mt-1">Here are your tools for a better day.</p>
        </div>
      </header>

      <main className="max-w-4xl mx-auto py-8 px-4">
        {/* --- NEW CONDITIONAL MESSAGE AND BUTTON --- */}
        {showPostSubmitMessage && (
            <div className="bg-green-100 border-l-4 border-green-500 text-green-700 p-4 rounded-lg mb-8 shadow-md" role="alert">
                <p className="font-bold">Thank you for your submission!</p>
                <p>Your well-being is important. If you feel you need to talk to someone, support is just a click away.</p>
                <button 
                    onClick={handleRequestChat} 
                    className="mt-4 bg-green-500 text-white font-bold py-2 px-4 rounded-lg hover:bg-green-600 transition"
                >
                    Talk to a Professional Now
                </button>
            </div>
        )}

        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {/* Stress Meter Card */}
            <div className="bg-white p-6 rounded-xl shadow-lg flex flex-col items-center text-center">
              <h2 className="text-2xl font-semibold mb-2 text-gray-800">Daily Stress Check-in</h2>
              <p className="text-gray-600 mb-6">Take a moment to reflect on your current stress level. Your insights help you understand your patterns.</p>
              <Link href="/student/stress-meter" className="w-full bg-indigo-600 text-white font-bold py-3 px-4 rounded-lg hover:bg-indigo-700 transition-transform transform hover:scale-105">
                Open Stress Meter
              </Link>
            </div>

            {/* Request a Chat Card */}
            <div className="bg-white p-6 rounded-xl shadow-lg flex flex-col items-center text-center">
              <h2 className="text-2xl font-semibold mb-2 text-gray-800">Need to Talk?</h2>
              <p className="text-gray-600 mb-6">Connect with a certified professional for a confidential conversation whenever you need support.</p>
              <button onClick={handleRequestChat} className="w-full bg-blue-500 text-white font-bold py-3 px-4 rounded-lg hover:bg-blue-600 transition-transform transform hover:scale-105">
                Request a Live Chat
              </button>
              {status && <p className="mt-4 text-center text-sm text-gray-500">{status}</p>}
            </div>
        </div>
      </main>
    </div>
  );
}

